// Metadata
// Fetch API data once for metadata
async function getStoreSettingsData() {
  return await axiosInstance.get(`settings/store-setting`);
}
export async function generateMetadata() {
  const headersList = await headers();
  const host = headersList.get("host");
  const protocol = headersList.get("x-forwarded-proto") || "https";
  const autoBaseUrl = `${protocol}://${host}`;

  try {
    // Get store settings
    const storeSettingsArray = await getStoreSettingsData();
    const storeSettings = storeSettingsArray?.data?.data?.[0] || {};

    // Social links from storeSettings
    const socialLinks = [
      storeSettings?.facebook || "",
      storeSettings?.instagram || "",
      storeSettings?.linkedin || "",
      storeSettings?.twitter || "",
      storeSettings?.youtube || "",
    ].filter(Boolean); // remove empty values

    // Logo / OG image
    let imageUrl =
      `${storeSettings?.siteUrl || autoBaseUrl}/img/logo.svg` ||
      "https://www.spurtcommerce.com/img/spurt_banner.png";

    if (storeSettings?.storeLogo && storeSettings?.storeLogoPath) {
      imageUrl = `${process.env.NEXT_PUBLIC_IMAGE_URL}?path=${storeSettings.storeLogoPath}&name=${storeSettings.storeLogo}&width=1200&height=630&fit=contain&bg=ffffff`;
    }

    // SEO Values
    const seoTitle =
      storeSettings?.metaTagTitle ||
      storeSettings?.siteName ||
      `Welcome to ${host}`;

    const seoDescription =
      storeSettings?.metaTagDescription || `Explore products at ${host}.`;

    const seoKeywords =
      storeSettings?.metaTagKeyword || `${host}, online store, products`;

    const seoUrl = storeSettings?.siteUrl || autoBaseUrl;

    return {
      ...createMetadata({
        title: seoTitle,
        description: seoDescription,
        keywords: seoKeywords,
        imageUrl,
        url: seoUrl,
        showRobots: true,
        baseUrl: seoUrl,
        socialLinks,
      }),
    };
  } catch (err) {
    console.error("generateMetadata error:", err);

    return {
      ...createMetadata({
        title: `Welcome to ${host}`,
        description: `Explore products at ${host}.`,
        keywords: `${host}, online store, products`,
        imageUrl:
          `${autoBaseUrl}/img/logo.svg` ||
          "https://www.spurtcommerce.com/img/spurt_banner.png",
        url: autoBaseUrl,
        showRobots: true,
        baseUrl: autoBaseUrl,
        socialLinks: [],
      }),
    };
  }
}

