//Utils
import { GetToken } from "@/utilities/cookies";
//Api
import { getcategory } from "@/api/serversideaction/category";
//Component
import Headers from ".";

const HeaderServer = async ({ searchParams }) => {
  const [token, category] = await Promise.all([GetToken(), getcategory()]);

  return (
    <Headers
      token={token}
      searchParams={searchParams}
      topCategories={category?.data}
    />
  );
};

export default HeaderServer;
